  color: #014178;
  text-decoration: none;
  cursor: pointer;
}

#homepage__right-link:hover {
  color: #012546;
  text-decoration: underline;
}

.fade-appear {
  opacity: 0;
  transition: opacity 2000ms ease;
  transform: translateX(-50px);
  display: flex;
}

.fade-appear.fade-appear-active {
  opacity: 1;
  transform: translateX(0px);
}

.fade2-appear {
  opacity: 0;
  transition: opacity 3000ms linear;
  transform: translateY(50px);
  /* display: inline-block; */
}

.fade2-appear.fade2-appear-active {
  opacity: 1;
  transform: translateY(0px);
}

.fade3-appear {
  opacity: 0;
  transition: opacity 3000ms linear;
  transform: translateY(50px);
  /* display: inline-block; */
}

.fade3-appear.fade3-appear-active {
  opacity: 1;
  transform: translateY(0px);
}

.fade4-appear {
  opacity: 0;
  transition: opacity 4000ms linear;
  transform: translateY(50px);
  /* display: inline-block; */
}

.fade4-appear.fade4-appear-active {
  opacity: 1;
  transform: translateY(0px);
}

.fade5-appear {
  opacity: 0;
  transition: opacity 4000ms linear;
  transform: translateY(50px);
  /* display: inline-block; */
}

.fade5-appear.fade5-appear-active {
  opacity: 1;
  transform: translateY(0px);
}

.login-wrapper {
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color:  #F1F1E6;
}

.login-container {
  position: relative;
  background-color: rgb(255, 255, 255, .5);
  min-width: 335px;
  max-width: 375px;
}

#login-container__label {
  font-size: 28px;
  padding-top: 25px;
  padding-left: 25px;
  margin-bottom: 15px;
}

.login-container__form {
  padding-right: 25px;
  padding-left: 25px;
}

.login-container__input {
  min-height: 42px;
  width: 100%;
  color: #111;
  background-color: #fff;
  border: 1px solid #ddd;
  padding: 8px 16px;
  font-size: 16px;
  border-radius: 3px;

}

.login-container__input:focus {
  box-shadow: 0 0 0 1px #014178;
  outline: 0;
}

.login-container__input-bad {
  outline: none;
  border: 1px solid red;
  box-sizing: border-box;
  width: 100%;
  color: #111;
  background-color: #fff;
  padding: 8px 16px;
  font-size: 16px;
}

#login-container__redirect {
  text-align: center;
  padding-bottom: 25px;
}

.login-container__warning{
  height: 20px
}

.login-container__button {
  height: 40px;
  width: 100%;
  text-align: center;
  margin-bottom: 20px;
  background-color: #014178;
  color: white;
  font-size: 15px;
  border-radius: 3px;
  transition: all 300ms cubic-bezier(0.175, 0.885, 0.335, 1);
}

.login-container__button:hover {
  background-color: #014178b1;
  color: #014178;
}

.login-container__button:focus {
outline: none;
}
