#footer-container {
  width: 100%;
  min-height: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  bottom: 0;
  background-color: #ffffff;
  border-top: solid 1px #d3d3d3;
}

#footer-container__left {
  width: 300px;
  padding-left: 20px;
}

#footer-container__middle {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 13px;
  font-weight: 300;
}

#footer-container__right {
  width: 300px;
  display: flex;
  justify-content: flex-end;
}

#footer-container__link {
  color: #014178;
  text-decoration: none;
  padding-right: 20px;
}

#footer-container__link:hover {
  color: #012546;
  text-decoration: underline;
}
