#meetings__table-header {
  height: 50px;
  min-height: 45px;
  width: 100%;
  background-color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  z-index: 35;
  border: 1px solid gray;
}

#meetings__table-header-check {
  padding-left: 18px;
  padding-top: 10px;
}

#meetings__table-label {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin-bottom: 5px;
  padding-top: 5px;
  padding-bottom: 5px;
  font-weight: 500;
}

#meetings-container {
  width: 100%;
  height: 60%;
  min-height: 30%;
  overflow-y: scroll;
}

#meetings-container__table {
  height: 100%;
  width: 100%;
  min-height: 25%;
  flex-direction: column;
  overflow-y: scroll;
  padding-bottom: 165px;
}

#meetings-container__meeting {
  display: flex;
  flex-direction: row;
  justify-content: space-around;

  width: 100%;
  height: 75px;
  border: solid 1px gray;
}

#meetings-container__meeting2 {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 100%;
  height: 75px;
  border: solid 1px gray;
  background-color: rgb(1, 65, 120, 0.5);
}

.meetings-container__text {
  width: 125px;
  margin-right: 5px;
}

.meetings-container__link {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;

  text-decoration: none;
}

.fa-arrow-circle-right {
  color: #014178;
}

.fa-arrow-circle-right:hover {
  color: #012546;
}
